.comment-form {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: start;
}

.comment-form .chat-emoji-list {
    left: -14%!important;
    top: 100%!important;
}

.comment-form .fa-smile {
    color: var(--primary-color);
    font-size: 25px;
    margin-right: 10px;
    margin-bottom: 0!important;
    padding: 10px;
    border-radius: 50%;
}

.comment-form .fa-smile:hover {
    background: var(--secondary-color);
    cursor: pointer;
}

.comment-form input[type=text] {
    height: 37px;
}

.comment-form button {
    height: 37px;
}
textarea{  
    overflow: hidden;
    width: 100%;
    font-size: 14px;
    margin: 10px;
    border-color: white;
    box-shadow: white;
    border: none;
    outline: none;

}