    box-shadow: 0 0 2px rgba(128, 128, 128, 0.5), inset 0 1px 1px var(--theme-base-color);
    cursor: default;
}

.tag-container .tag i {
    font-size: 16px;
    color: #666;
    margin-left: 5px;
    margin-bottom: 10px;
}

.tag-container textarea {
    padding: 5px;
    font-size: 16px;
    outline: none;
    border: none;
    color: var(--primary-grey);
    background: inherit;
    flex: 1 1;
}

.tag-container textarea::-webkit-input-placeholder {
    color: var(--secondary-grey);
}

.tag-container textarea:-ms-input-placeholder {
    color: var(--secondary-grey);
}

.tag-container textarea::-ms-input-placeholder {
    color: var(--secondary-grey);
}

.tag-container textarea::placeholder {
    color: var(--secondary-grey);
}

/* FRIENDS */

.profile-friends-container {
    width: 100%;
}

.profile-friends-container .profile-friend-list-item {
    border: none;
    border-bottom: 1px solid var(--border-color);
}
.profiles-filter-container {
    width: 100%;
    display: flex;
    background: var(--theme-base-color);
    padding: 15px 5.37%;
    margin-bottom: 15px;
}

.profiles-filter-container .profiles-filter-input {
    height: 40px;
    padding-left: 15px;
    border: none;
    border-radius: 20px;
    background: var(--background);
}

.profiles-filter-container .profiles-search-extra-options-icon {
    width: 50px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgba(0, 0, 0, 0.1)!important;
    border-radius: 20px;
    font-weight: bold!important;
    margin: 4px;
    background: var(--background)!important;
    color: var(--text-secondary)!important;
}

.profiles-filter-container .profiles-search-extra-options-icon:hover, .profiles-filter-container .profiles-search-extra-options-icon-active {
    background: var(--theme-base-color-hover)!important;
}

.profiles-filter-input:focus {
    outline: none;
    background: var(--theme-base-color);
    border: 1px solid var(--primary-color)!important;
}

.profile-img-container:hover, .main-profile-data:hover {
    cursor: pointer;
    transform: scale(1.01);
}

.profile-img-container {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    align-self: center;
}

.profile-row-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.profile-row-bottom {
    display: flex;
    text-align: start;
}

.profile-col {
    min-width: 20%;
    align-self: center;
    display: flex;
}

.main-profile-data {
    display: flex;
    flex-direction: column;
    text-align: start;
}

.settings-page-container {
    width: 100%;
    height: var(--content-height);
    display: flex;
}

.settings-page-container .alert {
    width: 100%;
    margin: 0!important;
    text-align: center;
}

.settings-page-container input[type=password] {
    width: 60%;
    border: none;
    outline: none;
    border-radius: 20px;
    background: var(--border-color);
    color: var(--primary-grey);
    margin-bottom: 20px;
    padding: 10px 20px 10px 20px;
}

.settings-page-container input[type=password]:focus {
    border: 1px solid var(--primary-color);
    background: var(--theme-base-color);
}

.settings-description-container {
    width: 60%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: var(--background);
}

/* BLOCK USER */

.settings-description-container .no-blocked-profiles {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.settings-description-container .blocked-profiles-list {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
}

.settings-description-container .blocked-profiles-list .blocked-profiles-title {
    width: 100%;
    position: -webkit-sticky;
    position: sticky;
    z-index: 1;
    top: 0;
    padding: 15px;
    text-align: center;
    background: var(--theme-base-color);
    border-bottom: 1px solid var(--border-color);
}

.settings-description-container .blocked-profiles-list .blocked-profile {
    border: none;
    border-bottom: 1px solid var(--border-color);
}

.settings-page-title-container {
    width: 100%;
    height: 20%;
    background: var(--theme-base-color);
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid var(--border-color);
}

/* CHANGE PASSWORD */

.change-password-form {
    background: var(--theme-base-color);
    width: 100%;
    height: 60%;
    padding: 20px 0;
}

.change-password-form button {
    width: 60%;
}

/* DELETE ACCOUNT */

.delete-account-confirmation {
    background: var(--theme-base-color);
    width: 100%;
    height: 40%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: large;
    padding: 20px 0;
    color: var(--primary-grey);
}

.delete-account-confirmation button {
    margin-top: 20px;
}

/* FAQ */
  
.faq-content {
    background: var(--theme-base-color);
    display: flex;
    align-items: center;
}

.faq-content .faq-center {
    background: var(--theme-base-color);
    width: 100%;
}

.faq-content details {
    background: var(--theme-base-color);
}

.faq-content summary {
    background: var(--theme-base-color);
    transition: background .75s ease;
    width: 100%;
    outline: 0;
    text-align: center;
    font-size: 50%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    border-bottom: 1px solid var(--border-color);
    padding: 5px;
}

.faq-content h2 {
    color: var(--primary-grey);
    text-align: left;
    margin-bottom: 0;
    padding: 8px;
    font: uni;
    text-shadow: none;
    font-size: 20px;
    font-weight: 100;
}

.faq-content details p {
    background: var(--theme-base-color);
    padding: 0 25px 15px 25px;
    margin: 0;
    text-shadow: none;
    text-align: justify;
    line-height: 1.3em;
    border-bottom: 1px solid var(--border-color);
}

.faq-content summary::after {
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    content: "\f107";
    display: inline-block;
    padding-right: 15px;
    font-size: 180%;
    color: var(--primary-grey);
}

.faq-content details[open] summary::after {
    content: '\f106';
    display: inline-block;
    padding-right: 15px;
    font-size: 100%;
}

.faq-content details[open] summary:hover {
    background:none;
}

.faq-content summary:hover {
    background: var(--theme-base-color-hover);
}

.faq-content details summary::-webkit-details-marker {
    display:none;
}

/* FEEDBACK */

@keyframes selectface {
    0% {
        width: 70px;
        height: 70px;
    }

    50% {
        transform: scale(1.25);
    }

    100% {
        width: 70px;
        height: 70px;
    }
}

.feedback-form {
    padding: 20px;
}

.feedback-form h3 {
    color: var(--primary-color);
}
.feedback-form .feedback-faces-container {
    background: var(--body-background);
    border-radius: 25px;
    text-align: center;
    padding: 20px;
}

.feedback-form .option-container {
    font-size: 50px;
    margin: 3px;
}

.feedback-form .option-container img {
    width: 70px;
    height: 70px;
}

.feedback-form .option-container .animated {
    animation-name: selectface;
    animation-duration: .5s;
    animation-fill-mode: forwards;
}

.feedback-form .option-container img:hover {
    cursor: pointer;
}

.feedback-form textarea {
    width: 100%;
    border: 1px solid var(--border-color);
    border-radius: 10px;
    padding: 10px;
    outline: none;
}

.sidebar {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 10%;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    height: 100vh;
    width: var(--sidebar-width);
    box-sizing: border-box;
    background: var(--fixed-components-background);
}

.sidebar .sidebar-menu-item {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: var(--primary-grey);
    font-size: 1.1rem;
    font-weight: bold;
    display: block;
    padding: 10px;
    margin: 5px 0;
    border-radius: 30px;
}

.sidebar .logo-container {
    display: flex;
    justify-content: start;
    align-items: center;
    height: var(--header-heigth);
    margin-left: 10px;
}

.sidebar ul .sidebar-menu-item .sidebar-menu-icon {
    font-size: 1.25rem!important;
    margin-right: 10px;
}

.sidebar ul .sidebar-menu-item .fas {
    width: 25px;
}

.sidebar ul a:hover > .sidebar-menu-item {
    background: var(--secondary-color);
    color: var(--primary-color);
    cursor: pointer;
}

.sidebar a.active .sidebar-menu-item{
    color: var(--primary-color)!important;
}


/* THEME SWITCHER */

.sidebar .checkbox {
	opacity: 0;
	position: absolute;
}

.sidebar .label {
	background-color: var(--primary-grey);
	border-radius: 50px;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 5px;
	position: relative;
	height: 26px;
    width: 50px;
}

.sidebar .label .ball {
	background-color: var(--theme-base-color);
	border-radius: 50%;
	position: absolute;
	top: 2px;
	left: 2px;
	height: 22px;
	width: 22px;
	transform: translateX(0px);
	transition: transform 0.2s linear;
}

.sidebar .checkbox:checked + .label .ball {
	transform: translateX(24px);
}

.sidebar .fa-moon {
	color: var(--theme-base-color);
}

.sidebar .fa-sun {
	color: var(--background);
}
@keyframes hide {
    0% {
        opacity: 1;
        max-height: 800px;
    }
    75% {
        opacity: 0;
        max-height: 800px;
    }
    100% {
        opacity: 0;
        max-height: 0px;
    }
}

.hide-animation {
    animation: hide 1.5s;
    animation-fill-mode: forwards;
    animation-play-state: paused;
}

@keyframes spin {
    to {
      transform: rotate(360deg);
    }
}

.spinner {
    animation: spin 1.3s ease-in-out infinite;
}

@media (max-width: 1165px) {
    .sidebar ul li {
        margin: 0;
    }
}

@media (max-width: 1130px) {
    .home-menu-btn-container .btn {
        font-size: .83rem;
    }
}

@media (max-width: 980px) {
    .wrapper .sidebar {
        display: none;
    }
    .wrapper .main-content .content .feed-create-post-form {
        display: none;
    }
    .wrapper .main-content .bottom-menu, .bottom-menu-action-icon {
