        display: none;
    }
    .post-list, .profile-list-container, .comment-list, .interest-list-container, .interest-profile-list,
    .interests-page-container, .settings-page-container, .settings-description-container, .notifications-container {
        padding-bottom: 10vh;
    }
    .list-group-item {
