    .profile-list-container .profile-row:hover {
        background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
        background: initial;
    }
    .post-container:hover {
        background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
        background: initial;
    }
    .post-actions .fa-comment:hover, .post-actions .fa-heart:hover {
        background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
        background: initial;
    }
    .post-actions .far:hover {
        color: var(--text-secondary);
    }
    .profile-page-menu .profile-page-menu-item:hover {
        background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
        background: initial;
    }
    .left-arrow-icon:hover, .add-icon:hover, .btn-secondary:hover {
        background: transparent none repeat 0 0 / auto auto padding-box border-box scroll!important;
        background: initial!important;
    }
    .profile-data-container .friend-btn:hover {
        background: #0b80d0!important;
    }
    .home-menu-btn-container .btn {
        font-size: 1rem;
    }
}

@media (max-width: 880px) {
    .home-menu-btn-container .btn {
        font-size: .86rem;
    }
}

@media (max-width: 790px) {
    .mobile-home-menu {
        display: unset;
    }
    .desktop-home-menu {
        display: none;
    }
    .home-menu-btn-container .btn {
        font-size: 1rem;
    }
}

@media (max-width: 750px) {
    .interest-item {
        flex-direction: column;
        align-items: flex-start;
    }
}

@media (max-width: 720px) {
    .list-group-item {
        padding: 1rem 2rem;
    }
    .messages-wrapper .chats-list {
        width: 100%;
        border: none;
    }
    .no-chat-selected, .messages-wrapper .loader-container {
        display: none!important;
    }
    .current-chat {
        width: 100vw!important;
        position: absolute;
    }
    .current-chat .current-chat-header {
        position: -webkit-sticky;
        position: sticky;
        top: var(--header-heigth);
    }
    .current-chat .current-chat-header .left-arrow-icon {
        display: unset!important;
    }
    .current-chat .send-message-container {
        position: fixed;
        bottom: 0;
        height: 8vh;
        background: var(--theme-base-color);
        padding: 10px;
    }
    .messages-wrapper .current-chat-header .left-arrow-icon {
        display: unset;
    }
}

@media (max-width: 635px) {
    .post-author-data-wrapper {
        flex-direction: column;
    }
    .profile-list-item-bio-button-wrapper {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        flex-direction: column;
    }
    .mobile-d-none {
        display: none!important;
    }
    .settings-page-menu {
        width: 100%!important;
    }
    .settings-description-container {
        width: 100%;
        position: absolute;
        z-index: 1;
    }
}


@media (max-width: 500px) {
    .post-list .post-container .post-row .post-col .post-author-data-wrapper {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 15px;
    }
    .profile-data-container {
        align-items: flex-start;
    }
    .profile-data-container .myprofile-btn-wrapper, .profile-data-container .profile-btn-wrapper {
        padding-top: 5%;
    }
    .profile-data-container .profile-view-more-select {
        top: 25%;
        right: 10%;
    }
    .interests-page-container h3 {
        font-size: 1.5rem;
    }
    .feedback-form .option-container {
        margin: 0;
    }
}

@media (max-width:450px) {
    .edit-profile-input {
        flex-direction: column;
    }
    .edit-profile-input .profile-field-label {
        width: 100%;
        text-align: center;
    }
}

@media (max-width: 375px) {
    .list-group-item {
        padding: .75rem 1.25rem;
    }
    .btn {
        font-size: .8rem;
        padding: .3rem .4rem;
    }
    .chat-message-submit {
        padding: .375rem .75rem;
    }
    .interests-page-container h3 {
        font-size: 1.4rem;
    }
}

@media (max-width:340px) {
    .interests-page-container h3 {
        font-size: 1.3rem;
    }
}

@media (max-width:330px) {
    .interests-page-container h3 {
        font-size: 1.2rem;
    }
}

@media (max-width: 320px) {
    .profile-data-container {
        padding: 3% 7.4%;
    }
    .profile-photo-input {
        font-size: .75rem;
    }
}
:root {
  --border-color: #f3f3f3;
  --background: #f3f3f3;
  --fixed-components-background: #fcfdfc;
  --heart-color: #E0245E;
  --heart-background-hover: rgba(224,36,94, .1);
  --primary-color: #48D1AF;
  --secondary-color: rgba(119, 147, 125, .1);
  --secondary-grey: #757575;
  --primary-color-hover: #3FB597;
  --primary-grey: #363636;
  --loader-background: rgba(119, 147, 125, .3);
  --theme-base-color: #fff;
  --theme-base-color-hover: rgba(250, 250, 250, 0.7);
  --view-more-select-border: rgba(0, 0, 0, .2);

  /*
  DARK THEME
  --border-color: #131313;
  --background: #131313;
  --fixed-components-background: #000;
  --heart-color: #E0245E;
  --heart-background-hover: rgba(224,36,94, .1);
  --primary-color: #48D1AF;
  --secondary-color: rgba(119, 147, 125, .1);
  --secondary-grey: #757575;
  --primary-color-hover: #3FB597;
  --primary-grey: #D9D9D9;
  --loader-background: rgba(119, 147, 125, .3);
  --theme-base-color: #000;
  --view-more-select-border: rgba(255, 255, 255, .2);
  */

  --header-heigth: 8vh;
  --content-heigth: 91vh;
  --sidebar-width: 16vw;
  --main-content-width: 59vw
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: 'Asap', sans-serif;
  transition: .1s!important;
}

html {
  font-size: 16px;
}

body {
  background: #fcfdfc;
  background: var(--fixed-components-background);
  color: #363636;
  color: var(--primary-grey);
  display: flex;
  justify-content: center;
  overflow-y: scroll;
}

btn, input {
  overflow: hidden;
}

h3 {
  font-weight: bold;
  color: #363636;
  color: var(--primary-grey);
}

a {
  font-weight: 400;
  color:  #48D1AF;
  color:  var(--primary-color);
}

a:hover {
  font-weight: 400;
  color: #3FB597;
  color: var(--primary-color-hover);
}

/* GENERAL UTILS */

.text-secondary {
  color: #757575!important;
  color: var(--secondary-grey)!important;
}

.word-break {
  text-align: start;
  word-wrap: break-word;
  word-break: break-all;
}

.notification-text-container {
  display: inline-block;
  position: relative;
  bottom: 10px;
  min-width: 15px;
  height: 18px;
  border-radius: 50%;
}

.notification-text-container .notification-text {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff!important;
  background-color: #f00!important;
  font-size: 0.75rem!important;
  border-radius: 50%;
  padding: 2px;
}

.emoji-list-container {
  display: none;
  position: absolute;
  left: 20px;
  z-index: 1;
  border: 2px solid rgba(20, 20, 20, 0.3);
}

/* DOCUMENT MAIN STRUCTURE */

.wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background: #f3f3f3;
  background: var(--background);
}

.wrapper .main-content {
  width: 59vw;
  width: var(--main-content-width);
  display: flex;
}

.wrapper .main-content .center {
  width: 100%;
  height: 100%;
  margin: 0 1vw;
}

.wrapper .main-content .center .header {
  height: 8vh;
  height: var(--header-heigth);
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  align-items: center;
  top: 0;
  padding: 20px 16% 20px 20px;
  background: #fcfdfc;
  background: var(--fixed-components-background);
  color: #363636;
  color: var(--primary-grey);
  font-size: 1.2rem;
}

.wrapper .main-content .center .content {
  position: relative;
  text-align: center;
}

/* PROFILE-IMAGE */

.profile-img-sm {
  border-radius: 50%;
  width: 48px;
  height: 48px;
}

.profile-img-med {
  border-radius: 50%;
  width: 64px;
  height: 64px;
}

.profile-img-big {
  border-radius: 50%;
  width: 96px;
  height: 96px;
}

.profile-img-sm:hover, .profile-img-med:hover {
  transform: scale(1.01);
}

/*
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px grey; 
  border-radius: 12px;
}
 
::-webkit-scrollbar-thumb {
  background: #48d1cc; 
  border-radius: 12px;
}

::-webkit-scrollbar-thumb:hover {
  background: #48d1cc; 
}
*/

